.advantages {
  background-color: #f8f9fc;
}

.advantages .container {
  @include vp-767 {
    padding: 0;
  }
}

.advantages__wrapper {
  max-width: 1040px;
  padding: 130px 0 110px;
  margin: 0 auto;

  @include vp-1199 {
    padding: 105px 0 95px;
  }

  @include vp-767 {
    padding: 70px 0 65px;
  }
}

.advantages__wrapper h2 {
  margin: 0 0 70px;
  font-family: $ff-alumni-sans;
  font-size: 54px;
  font-weight: 600;
  letter-spacing: 1.62px;
  text-align: center;

  @include vp-1199 {
    margin: 0 0 57px;
    font-size: 42px;
    letter-spacing: 1.26px;
  }

  @include vp-767 {
    font-size: 32px;
    letter-spacing: 0.96px;
  }
}

.advantages__list {
  display: grid;
  grid-template-columns: 1fr 23% 1fr;
  gap: 140px 40px;
  margin: 0;
  padding: 0;
  list-style-type: none;

  @include vp-1199 {
    grid-template-columns: 1fr 1fr;
    gap: 44px 30px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    gap: 39px;
    text-align: center;
  }
}

.advantages__primary {
  position: relative;
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 30px;
  margin-left: 20px;

  @include vp-1199 {
    margin: 0;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    gap: 0;
    padding: 0 34px;
    max-width: 100%;
    width: 100%;
  }
}

.advantages__primary:nth-child(5n+1),
.advantages__primary:nth-child(4n+2) {
  &::before {
    position: absolute;
    content: "";
    bottom: -67px;
    width: 100%;
    height: 2px;
    background-color: rgba(209, 10, 66, 0.2);

    @include vp-1199 {
      display: none;
    }
  }
}

.advantages__number p {
  margin: -20px 0 0 0;
  background: $text-gradient;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 71px;
  font-weight: 700;
  line-height: 106.5px;

  @include vp-1199 {
    margin: -15px 0 0;
    font-size: 58px;
    line-height: 87px;
  }

  @include vp-767 {
    margin: -18px 0 0;
  }
}

.advantages__primary span {
  display: block;
  margin: -25px 0 0;
  color: #2e35ac;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;

  @include vp-1199 {
    margin: -20px 0 0;
  }
}

.advantages__text {
  @include vp-767 {
    margin: 7px 0 0;
  }
}

.advantages__primary h3 {
  margin: 0 0 5px;
  font-size: 22px;
  font-weight: 700;
  line-height: 33px;

  @include vp-1199 {
    margin: 0;
    font-size: 20px;
    line-height: 30px;
  }
}

.advantages__text p {
  margin: 0;
  color: #102654;
  font-size: 16px;
  font-weight: 300;
  line-height: 22.4px;

  @include vp-767 {
    margin: 0 12px;
  }
}

.advantages__secondary {
  grid-column: 2 / 3;
  grid-row: 1 / 3;
  padding: 60px 34px 68px;
  margin: 5px 0 15px 0;
  background-color: #1c3374;
  color: #ffffff;
  text-align: center;

  @include vp-1199 {
    grid-column: 1 / 3;
    grid-row: 2 / 3;
    width: 65%;
    padding: 35px 135px 48px;
    margin: 0 auto;
  }

  @include vp-767 {
    grid-column: 1 / 2;
    grid-row: 3 / 4;
    margin: 6px 0;
    padding: 35px 70px 33px;
    width: 100%;
    min-height: 239px;
  }
}

.advantages__secondary p {
  margin: -24px 0 -16px;
  background: #ffffff;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 74px;
  font-weight: 700;
  line-height: 111px;

  @include vp-767 {
    margin: -17px 0 -23px;
  }
}

.advantages__secondary span {
  margin: -20px 0 30px 0;
  color: #ffffff;
  font-size: 26px;
  font-weight: 700;
  line-height: 39px;

  @include vp-1199 {
    margin: -20px 0 15px 0;
    font-size: 22px;
    line-height: 33px;
  }

  @include vp-767 {
    margin: -20px 0 0;
  }
}

.advantages__secondary h3 {
  margin: 31px 0 -10px;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;

  @include vp-1199 {
    margin: 13px 0 -10px;
  }

  @include vp-767 {
    margin: 8px 0 11px;
  }
}
